import React, { useState, useEffect } from 'react';
import { GRGContainer,GRGContent,GRGRow,Column1,TextWrapper,GRGHeadline,GRGParagraph, ButtonWrapper, Button, Column2, LiveData, LiveDataParagraph } from './GRGElements';
import axios from 'axios';
import BorderWrapper from 'react-border-wrapper';
// import ResponsivePlayer from './ResponsivePlayer';
import ReactPlayer from "react-player";

const GRG = () => {

    const url = 'https://games.roblox.com/v1/games?universeIds=1499870257'
    const [players, setPlayers] = useState(null)
    const MINUTE_MS = 60000;
    
    useEffect(() => {
        const interval = setInterval(() => {
            axios.get(url, { crossdomain: true, headers: {
                'Access-Control-Allow-Origin': '*', }})
            .then(response => {
            console.log(response.data)
            setPlayers(response.data)
        })
        }, MINUTE_MS);

        return () => clearInterval(interval); 
    }, [url])

    if(players){
        return (
            <GRGContainer id='grg'>
                <GRGContent>
                    <GRGRow imgStart={true}>
                        <Column1>
                            <TextWrapper>
                                <GRGHeadline>👾GENERIC ROLEPLAY GAEM.</GRGHeadline>
                                <GRGParagraph>GRG is a roleplay game where you start as a peasnat and make your way up to becoming a leader. Some of the many rules include rioting, violence and paying taxes.</GRGParagraph>
                                <ButtonWrapper>
                                    <Button href='https://www.roblox.com/games/4598019433/generic-roleplay-gaem'  target='_blank'>
                                       play GRG
                                    </Button>
                                    <Button href='https://www.roblox.com/groups/5574524/generic-roleplay-gaem-fanclub#!/about'  target='_blank'>
                                        GRG roblox group
                                    </Button>
                                    <Button href='https://discord.com/invite/grg'  target='_blank'>
                                        GRG discord
                                    </Button>
                                </ButtonWrapper>
                                <LiveData>
                                    <BorderWrapper borderColour='floralwhite' borderType='inset'>
                                        <LiveDataParagraph>active players:</LiveDataParagraph>
                                        <LiveDataParagraph style={{marginTop:'35px', fontSize:'40px'}}>{players.data[0].playing}</LiveDataParagraph>
                                    </BorderWrapper>
                                </LiveData>
                            </TextWrapper>
                        </Column1>
                        <Column2>
                        <ReactPlayer url="https://www.youtube.com/watch?v=_A7-KGfAQks" />
                        </Column2>
                    </GRGRow>
                </GRGContent>
            </GRGContainer>
        );  
    }

    return (
        <GRGContainer id='grg'>
            <GRGContent>
                <GRGRow imgStart={true}>
                    <Column1>
                        <TextWrapper>
                            <GRGHeadline>GENERIC ROLEPLAY GAEM👾</GRGHeadline>
                            <GRGParagraph>GRG is a roleplay game where you start as a peasnat and make your way up to becoming a leader. Some of the many rules include rioting, violence and paying taxes.</GRGParagraph>
                            <ButtonWrapper>
                                <Button href='https://www.roblox.com/games/4598019433/generic-roleplay-gaem' target='_blank'>
                                   play GRG
                                </Button>
                                <Button href='https://www.roblox.com/groups/5574524/generic-roleplay-gaem-fanclub#!/about' target='_blank'>
                                    GRG roblox group
                                </Button>
                                <Button href='https://discord.com/invite/grg' target='_blank'>
                                        GRG discord
                                </Button>
                            </ButtonWrapper>
                            <GRGParagraph></GRGParagraph>
                        </TextWrapper>
                    </Column1>
                    <Column2>
                    <ReactPlayer url="https://www.youtube.com/watch?v=_A7-KGfAQks" />
                    </Column2>
                </GRGRow>
            </GRGContent>
        </GRGContainer>
    );  

};

export default GRG;
